html {
	font-size: 62.5%;
}

body {
	color: #444;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif,
		"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	font-size: 1.6rem;
	line-height: 1.5rem;
	margin: 0;
	padding: 0;
}

#root {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	line-height: 1.125;
	margin-top: 3.2rem;
}



